'use strict';

// so works in browser
var pathParse = require('path-parse');
var cache = {};

function importAll(r) {
  r.keys().forEach(function (key) {
    return cache[pathParse(key).name] = r(key);
  });
}

importAll(require.context('pages/', true, /\.js$/));

module.exports = cache;


